import React from "react";
import Header from "../../../components/Header";
import Sidebar from "../../../components/Sidebar";
import Footer from "../../../components/Footer";
import "./links.css";
import { useData } from "../../../context/DataContext";
import Linke from "./Linke";
import { ContactInterface } from "../../../types/types";

const Links = () => {
  const { contact }: { contact?: ContactInterface[] } = useData();
  const showLinks = () => {
    return contact && contact.map((el, i) => <Linke {...el} key={i} />);
  };
  return (
    <div className="add_panel">
      <Header admin={true} />
      <Sidebar admin={true} />
      <div className="outer_container">
        <div className="adding_form container" id="accesspanel">
          <h1 id="litheader" className="light_heading">
            CONTACT DETAILS
          </h1>
          {showLinks()}
        </div>
        <Footer />
      </div>
    </div>
  );
};

export default Links;
